body {
  text-align: justify;
  font-family: Tahoma, sans-serif !important;
}

.background {
  z-index: -1;
  background-image: url("bg.d6227ce6.jpg");
  background-position: 0 bottom;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.blurred_backg {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
}

.custom_margintop {
  margin-top: 20vh;
}

.section_padding {
  padding-top: 100px;
}

.project_images {
  -webkit-filter: blur(3px);
}

.about_images {
  padding: 20px;
}

.bg_nav_foot {
  color: #fff;
  background: linear-gradient(to left, #386de0, #2157ce);
  outline: 5px solid #2157ce;
}

.intro_bg {
  background: linear-gradient(#fff9 20%, #fff0 80%);
  height: 100vh;
}

.intro_style {
  max-width: 600px;
  min-height: 60vh;
  margin: auto;
  padding: 10px;
  font-size: larger;
}

.card-body {
  min-height: 7.5rem;
}

.skill_img {
  max-width: 2.5em;
  margin-right: 7px;
}

.hidden {
  opacity: 0;
  transition: opacity 1s;
}

.fade-in {
  opacity: 1;
}

.card {
  box-shadow: none;
  transition: box-shadow .3s;
}

.card:hover {
  box-shadow: 0 0 10px #000000bf;
}

.rounded_edges {
  border-radius: 15px;
}

.rounded_top_edges {
  border-radius: 15px 15px 0 0 !important;
}

.very_rounded_edges {
  border-radius: 30px;
}

.custom_carousel_height {
  height: 31rem;
}

.nav__link {
  font-size: large;
  position: relative;
}

.nav__link:after {
  content: "";
  width: 0;
  height: 2px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 50%;
}

.nav__link:hover:after {
  width: 100%;
  left: 0;
}

.nav__link:hover {
  color: #fff;
  transition: all .3s ease-in-out;
}

.nav__link.hover-class:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link.hover-class {
  color: #fff;
  transition: all .3s ease-in-out;
}

.bottom_shadow {
  background: linear-gradient(to top, #2157ce4d 25%, #0000 75%);
}

.about_bg {
  background: linear-gradient(#0000004d, #0000);
}

.carousel__logo {
  cursor: pointer;
  width: 25%;
  margin: 0 auto;
  display: block;
  top: 50%;
  transform: translateY(-10%);
}

.footer_icons {
  color: #fff;
}

.footer_icons:hover {
  color: bisque;
  cursor: pointer;
}

.footer_text {
  color: #d3d3d3;
  font-size: smaller;
}

@media only screen and (width >= 3840px) {
  body {
    zoom: 220%;
  }

  .intro_style {
    max-width: 600px;
    min-height: 30vh;
    margin: auto;
  }

  .intro_bg {
    height: 22vh;
  }
}

.skill__info {
  cursor: pointer;
  width: 1.5em;
  margin: 3px;
  position: absolute;
  top: 0;
  right: 0;
}

@media only screen and (width >= 2560px) {
  body {
    zoom: 160%;
  }

  .intro_style {
    max-width: 600px;
    min-height: 40vh;
    margin: auto;
  }

  .intro_bg {
    height: 60vh;
  }
}

#skills .card .card-text {
  display: none;
}

@media only screen and (width <= 40rem) {
  .section_padding {
    padding-top: 200px;
  }

  .custom_margintop {
    margin-top: 10vh;
  }

  .about_images {
    align-content: flex-end;
    max-width: 4rem;
    margin: 0;
    padding: 5px 20px 0 0;
    position: absolute;
    top: 0;
    right: 0;
  }

  .skill_img {
    height: 1.5em;
    margin-right: 7px;
  }

  #skills .card-title {
    text-align: left;
    font-size: smaller;
  }

  .card-body {
    min-height: 4.5em;
  }

  .carousel {
    flex-direction: column;
    width: 100%;
    height: 70vh;
    display: flex;
  }

  .carousel-inner, .carousel-item {
    height: 100%;
  }

  .carousel-indicators {
    display: none;
  }

  .project_images {
    -webkit-filter: blur(3px);
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  .carousel-caption p {
    display: none;
  }

  .carousel-caption {
    justify-content: center;
    display: flex !important;
  }

  .carousel-inner img {
    width: 10rem;
  }
}

