  outline: solid 1px red;
} */

body {
  font-family: "Tahoma", sans-serif !important;
  text-align: justify;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 bottom;
}

.blurred_backg {
  backdrop-filter: blur(4px);
}
.custom_margintop {
  margin-top: 20vh;
}

.section_padding {
  padding-top: 100px;
}

.project_images {
  filter: blur(8px);
  -webkit-filter: blur(3px);
}

.about_images {
  padding: 20px;
}

.bg_nav_foot {
  background: linear-gradient(to left, #386de0, rgb(33, 87, 206));
  outline: solid 5px rgb(33, 87, 206);
  color: white;
}

.intro_bg {
  height: 100vh; /* always set when zoomed in */
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.6) 20%,
    rgba(255, 255, 255, 0) 80%
  );
}

.intro_style {
  max-width: 600px;
  min-height: 60vh;
  margin: auto;
  padding: 10px;
  font-size: larger;
}

.card-body {
  min-height: 7.5rem;
}

.skill_img {
  max-width: 2.5em;
  margin-right: 7px;
}

.hidden {
  opacity: 0;
  transition: opacity 1s;
}

.fade-in {
  opacity: 1;
}

.card {
  transition: box-shadow 0.3s ease;
  box-shadow: none;
}

.card:hover {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}

.rounded_edges {
  border-radius: 15px;
}

.rounded_top_edges {
  border-radius: 15px 15px 0 0 !important;
}

.very_rounded_edges {
  border-radius: 30px;
}

.custom_carousel_height {
  height: 31rem;
}

.nav__link {
  position: relative;
  font-size: large;
}

.nav__link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  transition: all 0.3s ease-in-out;
}

.nav__link:hover::after {
  left: 0;
  width: 100%;
}

.nav__link:hover {
  color: #fff;
  transition: all 0.3s ease-in-out;
}

.nav__link.hover-class::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease-in-out;
}

.nav__link.hover-class {
  color: #fff;
  transition: all 0.3s ease-in-out;
}

.bottom_shadow {
  background: linear-gradient(
    to top,
    rgba(33, 87, 206, 0.3) 25%,
    rgba(0, 0, 0, 0) 75%
  );
}

.about_bg {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0));
}

.carousel__logo {
  cursor: pointer;
  width: 25%;
  display: block;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-10%);
}

.footer_icons {
  color: #fff;
}

.footer_icons:hover {
  color: bisque;
  cursor: pointer;
}

.footer_text {
  color: lightgrey;
  font-size: smaller;
}

/* 4k */
@media only screen and (min-width: 3840px) {
  body {
    zoom: 220%;
  }
  .intro_style {
    max-width: 600px;
    min-height: 30vh;
    margin: auto;
  }
  .intro_bg {
    height: 22vh;
  }
}

.skill__info {
  width: 1.5em;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  margin: 3px;
}

/* wide */
@media only screen and (min-width: 2560px) {
  body {
    zoom: 160%;
  }
  .intro_style {
    max-width: 600px;
    min-height: 40vh;
    margin: auto;
  }
  .intro_bg {
    height: 60vh;
  }
}

#skills .card .card-text {
  display: none;
}

/* mobile screen */
@media only screen and (max-width: 40rem) {
  .section_padding {
    padding-top: 200px;
  }

  .custom_margintop {
    margin-top: 10vh;
  }

  .about_images {
    /* display: flex; */
    position: absolute;
    align-content: flex-end;
    max-width: 4rem;
    margin: 0;
    padding: 5px 20px 0px 0px;
    top: 0;
    right: 0;
  }

  /* .text__toggler {
    height: 6rem;
  } */

  .skill_img {
    height: 1.5em;
    margin-right: 7px;
  }

  #skills .card-title {
    text-align: left;
    font-size: smaller;
    /* height: 1.5rem; */
  }

  .card-body {
    min-height: 4.5em;
  }

  .carousel {
    display: flex;
    flex-direction: column;
    height: 70vh;
    width: 100%;
  }

  .carousel-inner {
    height: 100%;
  }

  .carousel-item {
    height: 100%;
  }

  .carousel-indicators {
    display: none;
  }

  .project_images {
    filter: blur(8px);
    -webkit-filter: blur(3px);
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  .carousel-caption p {
    display: none;
  }

  .carousel-caption {
    display: flex !important;
    justify-content: center;
  }

  .carousel-inner img {
    width: 10rem;
  }
}
